import "./styles.css";
import { useState, useEffect } from "react";

export default function App() {
  const item = {
    questions: []
  };
  const [elements, setElements] = useState(() => {
    let eleArr = [];
    item.questions.map((e, i) => {
      const key = Date.now() + i;
      if (e.type === "checkbox" || e.type === "radio") {
        let optArr = [];
        e.options.map((option, j) => {
          const oKey = Date.now() + i + j;
          optArr.push({ value: "item", key: oKey });
        });
        eleArr.push({
          type: e.type,
          options: optArr,
          key: key
        });
      } else if (e.type === "content") {
        eleArr.push({
          type: e.type,
          options: [e.title],
          key: key
        });
      } else if (e.type === "rating") {
        eleArr.push({
          type: e.type,
          descHigh: e.descHigh,
          descLow: e.descLow,
          scaleDesc: e.scaleDesc,
          options: [],
          key: key
        });
      } else {
        eleArr.push({
          type: e.type,
          options: [],
          key: key
        });
      }
    });
    return eleArr;
  });

  const handleRemove = (index, element) => {
    console.log(index);
    console.log(elements);
    setElements((elements) => elements.filter((e, i) => i !== index));

    //remove formik value manually
  };

  return (
    <div className="App">
      {elements.map((e, i) => {
        if (e.type === "text" || e.type === "textarea" || e.type === "rating") {
          return (
            <AssessmentContent1
              key={e.key}
              item={e}
              index={i}
              removeItem={() => handleRemove(i)}
              formik={formik}
            />
          );
        } else if (e.type === "content") {
          return (
            <AssessmentContent3
              key={e.key}
              item={e}
              addContent={(e) => addContent(e, i)}
              removeItem={() => handleRemove(i)}
            />
          );
        } else {
          return (
            <AssessmentContent2
              key={e.key}
              item={e}
              index={i}
              formik={formik}
              removeItem={() => handleRemove(i)}
              addSubItem={() => handleAddSubItem(i)}
              removeSubitem={(j) => handleRemoveSubItem(i, j)}
            />
          );
        }
      })}
    </div>
  );
}
